
import _ from 'lodash';
import md5 from 'vux/src/tools/md5/index.js'

import { setLocalStorageCache } from '../components/utils/CacheService';

export default {
name: 'login',
data() {
return {
selectIndex: this.$route.params.userType - 1,
mobile: '',
password: '',
type: this.$route.params.userType,
loginDisabled: true
};
},
methods: {
onSubmit(e) {
e.preventDefault();

const params = {
mobile: this.mobile,
password: md5(this.password),
type: this.type
};

this.$http.post('/user/login', params)
.then((res) => {
const data = _.get(res, 'data', {});

if (data.status === 200) {
setLocalStorageCache('user', this.mobile);
setLocalStorageCache('userType', this.type);
this.$router.push('/');
} else {
this.$vux.toast.show({
type: 'cancel',
width: '7.6em',
text: data.message || '登录失败'
});
}
});
},
setLoginDisabled() {
if (!(this.mobile && this.password)) {
this.loginDisabled = true;
} else {
this.loginDisabled = !(
this.$refs.mobile.valid && this.$refs.password.valid
);
}
},
selectTab(index) {
this.type = index + 1;
this.selectIndex = index;
}
}
};



