import { removeLocalStorageCache } from '../components/utils/CacheService';

export default {
  name: 'user-center',
  methods: {
    logout() {
      removeLocalStorageCache('user');
      removeLocalStorageCache('type');
      removeLocalStorageCache('token');

      this.$router.push('/');
    }
  }
};



