import _ from 'lodash';
import moment from 'moment';

export default {
  name: 'home',
  data() {
    return {
      goodsList: []
    };
  },
  mounted() {
    this.getGoods();
  },
  methods: {
    getTime(timestamp) {
      return moment(timestamp).format('YYYY-MM-DD');
    },
    getGoods() {
      this.$http.get('/goods/getGoods?status=0')
        .then((res) => {
          const data = _.get(res, 'data', {});

          if (data.status === 200) {
            this.goodsList = data.data;
          } else {
            this.$vux.toast.show({
              type: 'cancel',
              width: '7.6em',
              text: data.message || '查询货源失败'
            });
          }
        });
    }
  }
};



