import './assets/font/iconfont.js';

export default {
  name: 'app',
  beforeCreate() {
    // 设备判断
    const u = navigator && navigator.userAgent;
    const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    this.$root.isiOS = isiOS;
  }
};



