import { getLocalStorageCache } from '../components/utils/CacheService';

export default {
  data() {
    const userType = getLocalStorageCache('userType');
    return {
      userType
    };
  },
  methods: {
    getTitle() {
      switch (this.$route.path) {
        case '/':
          return '首页';
        case '/addGoods':
          return '添加货源';
        case '/center':
          return '个人中心';
        default:
          return '';
      }
    }
  }
};



