
import _ from 'lodash';
import md5 from 'vux/src/tools/md5/index.js'


export default {
name: 'register',
data() {
return {
selectIndex: 0,
mobile: '',
company: '',
idCard: '',
password: '',
confirmPassword: '',
username: '',
type: 1,
registerDisabled: true
};
},
methods: {
idCardValidate() {
return {
valid: this.idCard && /^\d{6}(18|19|20)?\d{2}(0[1-9]|1[12])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(this.idCard),
msg: '身份证格式错误'
};
},
validateMsg() {
if (this.password !== this.confirmPassword) {
return '两次输入的密码不同，请确认';
}
return null;
},
onSubmit(e) {
e.preventDefault();

const msg = this.validateMsg();

if (msg) {
this.$vux.toast.show({
text: msg
});
return;
}

const params = {
mobile: this.mobile,
company: this.company,
idCard: this.idCard,
username: this.username,
password: md5(this.password),
confirmPassword: md5(this.confirmPassword),
type: this.type
};

this.$http.post('/user/register', params)
.then((res) => {
const data = _.get(res, 'data', {});

if (data.status === 200) {
this.$vux.toast.show({
type: 'success',
width: '7.6em',
text: '成功注册，请登录'
});
this.$router.push(`/login/${this.type}`);
} else {
this.$vux.toast.show({
type: 'cancel',
width: '7.6em',
text: data.message || '注册失败'
});
}
});
},
setRegisterDisabled() {
if (!(this.mobile && this.password && this.username &&
this.confirmPassword && this.idCard &&
(this.company || this.type === 2))) {
this.registerDisabled = true;
} else {
this.registerDisabled = !(
this.$refs.mobile.valid &&
this.$refs.idCard.valid
);
}
},
selectTab(index) {
this.type = index + 1;
this.selectIndex = index;
}
}
};



